* {
  box-sizing: border-box;
  margin: 0;
}

.container {
  max-width: 1120px;
  min-width: 991px;
  margin: 0 auto;
  padding: 0 15px;
}

.body {
  font-family: 'Courier New', Courier, monospace;
  font-weight: 400;
  font-size: 14px;
  font-style: normal;
}

.main {
  border-color: aqua;
}

.main .wrapper {
  display: flex;
  margin: 0 15px;
  flex-wrap: wrap;
}

.main .inner {
  width: 33.333%;
  padding: 0 15px;
}

.main .cart {
  border-color: azure;
}

.main .cart__title {
  font-size: 55px;
  font-weight: 57px;
}

.main .cart__text {
  font-size: 25px;
  color: blue;
}
